import {
  ADD_NODE_UPDATE,
  ADD_REGION,
  CONNECTING,
  CONNECTED,
  CONNECTION_ERROR,
  DISCONNECTED,
  UPDATE_LAST_BLOCK_INFO
} from "../action-types";

const initialState = {
  status: "disconnected",
  lastBlock: {
    height: null,
    hash: null,
    timestamp: null
  },
  regions: {},
  nodes: [],
  updates: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case ADD_NODE_UPDATE: {
      let { hostname, ...update } = action.payload;
      return {
        ...state,
        nodes: [
          ...state.nodes,
          ...(!(hostname in state.updates) ? [hostname] : [])
        ],
        updates: {
          ...state.updates,
          [hostname]: [update, ...(state.updates[hostname] || [])]
        }
      };
    }

    case ADD_REGION: {
      const nodes = state.regions[action.payload.region] || [];

      return {
        ...state,
        regions: {
          ...state.regions,
          [action.payload.region]: [
            ...nodes,
            ...(nodes.includes(action.payload.hostname)
              ? []
              : [action.payload.hostname])
          ]
        }
      };
    }

    case UPDATE_LAST_BLOCK_INFO: {
      let { height, hash, timestamp } = action.payload;

      return height > state.lastBlock.height
        ? {
            ...state,
            lastBlock: {
              height,
              hash,
              timestamp
            }
          }
        : state;
    }

    case CONNECTING: {
      return {
        ...state,
        status: "connecting"
      };
    }

    case CONNECTED: {
      return {
        ...state,
        status: "connected"
      };
    }

    case DISCONNECTED: {
      return {
        ...state,
        status: "disconnected"
      };
    }

    case CONNECTION_ERROR: {
      return {
        ...state,
        status: "error"
      };
    }

    default:
      return state;
  }
}
